import React from 'react';
import './App.css';
import logo from './zattire-logo.png';
import FormTemplate from "./Components/formTemplate.jsx"
import questions from "./Components/questions.js"
import Axios from 'axios';


export default class App extends React.Component {

  constructor(props) {
    super();
    this.state = {
      start: false,
      finished: false,
      laoding: false,
      count: 0,
      prev: []
    }

    // function binfing
    this.startOnboarding = this.startOnboarding.bind(this);
    this.prevQues = this.prevQues.bind(this)
  }

  // this function  
  startOnboarding(params) {
    this.setState({
      start: true,
      count: 0
    });
  }

  // this will render new questions. 
  renderQuestions() {
    if (this.state.loading)
      return (<p> Loading ...</p>)

    return (
      <FormTemplate
        count={this.state.count + 1}
        nextQues={(data, limit, name) => this.nextQues(data, limit, name)}
        prevQues={() => this.prevQues()}
        questions={questions[this.state.count]}
        limit={questions.length - 1 === this.state.count ? true : false}
      />)
  }

  prevQues() {
    if (this.state.prev[0] ===12) {
      if(this.state.prev.length >= 0){
        this.setState({
          count: 12,
          prev: []
        })
      }else{
        let newCount = this.state.prev[0];
        this.state.prev.slice(0, 1);
  
        this.setState({
          count: newCount,
          prev: this.state.prev
        })
      }
      
    } else this.setState({count: this.state.count - 1});
    
  }

  async nextQues(data, limit, name) {
    // Answer
    console.log("DATA TO BE SENT", data, "LIMIT", limit);
    this.setState({ loading: true });
    if (this.state.count >= 12) {
      this.state.prev.push(this.state.count);
      this.setState({ prev: this.state.prev });
    }
    /* 
      if array has elements greater than 1 then send the array as at is else
      extract data from index[0] and send the data as string. 
    */
   let val = data
   if(data.length === 1){
     val = data[0]
   }

    try{
      const id = localStorage.getItem("token")
      const data = {
        [name]: val
      }
      if(!id){
        const res = await Axios.post("/create-vendor", data)
        if(res.status === 200){
          const token = res.data._id
          console.log(token)
          localStorage.setItem("token", token)
        }
      }else{
        const res = await Axios.put(`/update-vendor/${id}`, data)
        if(res.status === 200){
          console.log("Done");
          
        }
      }
      if (this.state.count < questions.length - 1) {
        if (limit) {
          this.setState({ start: false, finished: true });
        }
        let newCount = this.state.count + 1
        if (this.state.count === 12) {
          const firstData = data[0];
          for (let i = this.state.count; i < questions.length; i++) {
            const q = questions[i]
            if (q.question === `${firstData} PRICING`) {
              newCount = i;
              break;
            }
          }
        }
        this.setState({

          count: newCount,
          loading: false
        })
      } else {
        this.setState({ loading: false });
        if (limit) {
          this.setState({ start: false, finished: true });
        }

      }
      
    }catch(e){
      console.log(e.message)
    }
  }

  componentDidMount(){
    localStorage.removeItem("token")
    questions.map((q) => {
      console.log(q.question.toLowerCase().replace(" ", "_").replace(" ", "_").replace(" ", "_").replace(" ", "_").replace(" ", "_").replace(" ", "_").replace(" ", "_").replace(" ", "_").replace(" ", "_").replace(" ", "_").replace(" ", "_").replace(" ", "_").replace(" ", "_").replace(" ", "_"))
    })
  }

  render() {
    return (
      <div className="App scrolling">
        <header className="App-header">
          {this.state.start ?
            <div>
              <img src={logo} className="logos onboarding-logo" alt="logo" />
              {this.renderQuestions()}

            </div>
            :
            <div>
              <img src={logo} className="zattire-logo" alt="logo" />

              {
                !this.state.finished ? <p className="thankyou"> An online store locator for fashion designers, make up artists and salons. </p> :
                  <p className="thankyou"> Thank you! Your form has been successfully submited. Our team will connect to you shorty. </p>
              }

              {
                !this.state.finished ?
                  <div onClick={() => this.startOnboarding()} className="zattire-button">
                    <center>Let's start</center>
                  </div> : ""
              }

            </div>}
        </header>

      </div>
    )
  }
}
